.input-field {
  position: relative;
  height: 500px;
  width: 100%;
}

.input-field textarea[type="text"] {
  position: absolute;
  background: transparent;
  box-shadow:  none;
  border: 1px white solid;
  border-radius: 5px;
  font-size: 16px;
  color: #fff;
  width: 100%;
  margin-top: 30px;
  height: 450px;
}

.input-field label {
  padding: 30px 5px;
  position: absolute;
  top: 0;
  left: 0;
  color: #555;
  pointer-events: none;
  display: block;
  
  transition: .5s;
  -webkit-transition: .5s;
  -moz-transition: .5s;
  -ms-transition: .5s;
  -o-transition: .5s;
  
  border-radius: 5px;
}

.input-field textarea[type="text"]:focus + label,
.input-field textarea[type="text"]:valid + label {
  -webkit-transform: translateY(-50px);
  -moz-transform: stranslateY(-50px);
  -ms-transform: translateY(-50px);
  -o-transform: translateY(-50px);
  transform: translateY(-50px);
  
  font-size: 14px;
  color: #fff;
  background: #ff006a;
  padding: 6px 10px;
}

.input-field span {
  position: absolute;
  bottom: 0;
  right: 0;
  display: block;
  background-color: #555;
  width: 100%;
  height: 2px;
}

.input-field span::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #00b0ff;
  
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  transform: scaleX(0);
  
  -webkit-transform-origin: right;
  -moz-transform-origin: right;
  -ms-transform-origin: right;
  -o-transform-origin: right;
  transform-origin: right;
  
  -webkit-transition: transform .5s ease-in-out;
  -moz-transition: transform .5s ease-in-out;
  -ms-transition: transform .5s ease-in-out;
  -o-transition: transform .5s ease-in-out;
  transition: transform 0.5s ease-in-out;
}

.input-field textarea[type="text"]:focus ~ span:before,
.input-field textarea[type="text"]:valid ~ span:before {
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
  transform: scaleX(1);
  
  -webkit-transform-origin: left;
  -moz-transform-origin: left;
  -ms-transform-origin: left;
  -o-transform-origin: left;
  transform-origin: left;
  
  -webkit-transition: transform .5s ease-in-out;
  -moz-transition: transform .5s ease-in-out;
  -ms-transition: transform .5s ease-in-out;
  -o-transition: transform .5s ease-in-out;
  transition: transform 0.5s ease-in-out;
}

.btn {
